<template>
  <div class="order-detail-view">
    <section class="order-detail-view-contanier">
      <header-view :title="title" :routerName="routerName"></header-view>
      <section class="order-detail-bg order-detail-ok-bg" :style="style"><span class="order-detail-state">{{stateMes}}</span></section>
      <div class="refund-progress" @click="oper(4)" v-if="spObj.state == '退款中'">取消/退款进度：{{progress}}<i class="icon iconfont icon-more fr"></i></div>
      <section class="order-detail-mes">
        <div class="order-detail-address"><label>提货地址</label><span>{{spObj.storeAddress}}<i></i></span></div>
        <div class="order-detail-datetme"><label>提货时间</label><span>{{spObj.takeTime}}</span></div>
      </section>
      <section class="order-dp">
        <header class="my-order-list-storeName"><i></i>{{spObj.storeName}}<span class="order-state">{{spObj.orderStatus}}</span></header>
        <div class="sp-contanier">
          <div class="hi-life-list-item" v-for="(item,key) in spObj.items">
            <div class="hi-life-list-item-img"><img :src="item.imageUrl"></div>
            <div class="hi-life-list-item-content">
              <div class="hi-life-list-item-summary">{{item.summary}}</div>
              <div class="hi-life-list-detail"><span class="sp-sales">￥{{item.sellPrice}}</span><span class="sp-count">x{{item.amount}}</span></div>
            </div>
          </div>
        </div>
      </section>
      <section class="order-detail-summary">
        <div>商品金额<span>{{spObj.totalPrice}}</span></div>
        <div>抵扣福分<span>{{spObj.joyScore}}</span></div>
        <div>付款方式<span>{{spObj.payType}}</span></div>
      </section>
      <section class="pay-money">实付金额<span>{{spObj.payMoney}}</span></section>
      <section class="order-pay-summary" v-if="spObj.state!= 2">
        <header>订单密码:<span>{{spObj.password}}</span></header>
        <div class="sj-code">
          <div class="sj-code-border">
            <div class="sj-code-img"><img :src="spObj.qrCode"></div>
            <div class="sj-code-help">商家扫码即可消费</div>
          </div>
        </div>
      </section>
      <footer>
        <div>订单编号:<span>{{spObj.orderNo}}</span></div>
        <div>创建时间:<span>{{spObj.createDate}}</span></div>
        <div v-if="spObj.payTime!= ''">付款时间:<span>{{spObj.payTime}}</span></div>
      </footer>
      <section class="bottom-btn" v-if="isSowBtn">
        <div @click="oper(spObj.state)" class="oper-btn-default" :class="{'oper-btn':spObj.state==2}">{{btnText}}</div>
      </section>
    </section>
  </div>
</template>

<script>
  import {getOrder} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '订单详情',
        routerName: 'myOrder',
        img: './image/code.png',
        orderNum: '123456789',
        createDate: '2017-08-24 15:36',
        payDate: '2017-08-24 15:50',
        okBg: 'url(/static/image/orderDetail/finish_bg@3x.png)',
        notOKBg: 'url(/static/image/orderDetail/obligation_bg@3x.png)',
        payGb: 'url(/static/image/orderDetail/paid_bg@3x.png)',
        style: {},
        spObj: {},
        youhui: 1,
        progress: '退款处理中'
      }
    },
    created: function () {
      var _this = this
      getOrder(this.$route.query.orderNo)
        .then(res => {
          _this.spObj = res.data
          if (_this.spObj.orderStatus === '交易完成') {
            _this.style.background = _this.okBg
            _this.style.backgroundSize = '100% 100%'
          } else if (_this.spObj.orderStatus === '已付款') {
            _this.style.background = _this.payGb
            _this.style.backgroundSize = '100% 100%'
          } else {
            _this.style.background = _this.notOKBg
            _this.style.backgroundSize = '100% 100%'
          }
        })
        .catch(function () {
          _this.$toast('加载失败')
        })
    },
    computed: {
      orderSalesCount: function () {
        let count = 0
        for (var key in this.spObj.spList) {
          count += this.spObj.spList[key].count * this.spObj.spList[key].sales
        }
        return count
      },
      realPayMoney: function () {
        let count = 0
        for (var key in this.spObj.spList) {
          count += this.spObj.spList[key].count * this.spObj.spList[key].sales
        }
        return count - this.youhui
      },
      isSowBtn: function () {
        if (this.spObj.orderStatus === '未付款' || this.spObj.orderStatus === '交易完成' || this.spObj.orderStatus === '关闭订单') {
          return false
        } else {
          return true
        }
      },
      btnText: function () {
        if (this.spObj.orderStatus === '已付款') {
          return '申请退款'
        }
        if (this.spObj.orderStatus === '未付款') {
          return '立即支付'
        }
      },
      stateMes: function () {
        if (this.spObj.orderStatus === '关闭订单') {
          return '该订单已关闭'
        }
        if (this.spObj.orderStatus === '已退款') {
          return '卖家已退款'
        }
        if (this.spObj.orderStatus === '正在退款') {
          return '卖家退款中'
        }
        if (this.spObj.orderStatus === '等待付款') {
          return '买家待付款'
        }
        if (this.spObj.state === '已付款') {
          return '买家已付款'
        }
        if (this.spObj.orderStatus === '交易成功') {
          return '交易完成'
        }
      }
    },
    methods: {
      oper: function (val) {
        if (val === '2') {
          this.$router.push({
            name: 'pay'
          })
        }
        if (val === '3') {
          this.$router.push({
            name: 'refund'
          })
        }
        if (val === 4) {
          this.$router.push({
            name: 'refundDetails'
          })
        }
      }
    }
  }
</script>

<style>
  /* 订单详情 */
  
  .order-detail-view {
    height: 100%;
    padding-top: .4rem;
  }
  
  .order-detail-view-contanier {
    height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  .order-detail-bg {
    height: 1rem;
    line-height: 1rem;
  }
  
  .order-detail-state {
    margin-left: 20%;
    color: #333;
  }
  
  .order-detail-mes {
    padding-left: .15rem;
    border-bottom: .05rem solid #f5f5f5;
  }
  .order-state{
    float: right;
    color: rgb(255, 161, 38);
  }
  .order-detail-mes div {
    height: .45rem;
    clear: both;
    padding-right: .15rem;
    line-height: .45rem;
    border-bottom: .01rem solid #E5E5E5;
  }
  
  .order-detail-mes div:last-child {
    border-bottom: none;
  }
  
  .order-detail-mes div label {
    color: #333;
  }
  
  .order-detail-mes div span {
    float: right;
  }
  
  .order-detail-mes div span>i {
    display: inline-block;
    margin-left: .05rem;
    width: .14rem;
    height: .16rem;
    vertical-align: middle;
    background: url(/static/image/select_city_now_location@3x.png);
    background-size: 100% 100%;
  }
  
  .order-detail-view .my-order-list-storeName {
    padding-left: .15rem;
  }
  
  .order-detail-summary {
    height: .885rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    padding-left: .15rem;
    flex-direction: column;
    align-items: center;
  }
  
  .order-detail-summary div {
    flex: 1;
    width: 100%;
    padding-right: .15rem;
    flex-shrink: 0;
    vertical-align: middle;
  }
  
  .order-detail-summary div:last-child {
    border-bottom: .01rem solid #E5E5E5;
  }
  
  .order-detail-summary div span,
  .pay-money span {
    float: right;
    color: #333;
  }
  
  .pay-money {
    color: #333;
    height: .45rem;
    line-height: .45rem;
    padding: 0 .15rem;
    border-bottom: .05rem solid #f5f5f5;
  }
  
  .pay-money>span {
    color: rgb(255, 161, 38);
  }
  
  .order-pay-summary {
    padding-left: .15rem;
  }
  
  .order-pay-summary>header {
    height: .5rem;
    line-height: .5rem;
    border-bottom: .01rem solid #E5E5E5;
  }
  
  .order-pay-summary>header>span {
    color: rgb(255, 161, 38);
    margin-left: .1rem;
  }
  
  .sj-code {
    height: 2.15rem;
    border-bottom: .01rem solid #E5E5E5;
  }
  
  .sj-code-border {
    position: relative;
    margin: .15rem auto;
    height: 1.815rem;
    width: 1.815rem;
    padding: .13rem;
    background: url(/static/image/orderDetail/QRcode_bg@3x.png);
    background-size: 100% 100%;
  }
  
  .sj-code-help {
    position: absolute;
    height: .5rem;
    left: 0;
    bottom: 0;
    right: 0;
    transform: translateY(50%);
    color: rgb(255, 161, 38);
    line-height: .5rem;
    text-align: center;
  }
  
  .order-detail-view footer {
    margin-top: .08rem;
    padding-left: .15rem;
    padding-bottom: .7rem;
  }
  
  .order-detail-view footer div {
    padding: .06rem 0;
  }
  
  .order-detail-view footer div span {
    margin-left: .1rem;
  }
  
  .bottom-btn {
    position: fixed;
    height: .5rem;
    padding: .1rem 0;
    width: 100%;
    bottom: 0;
    z-index: 1;
    border-top: .01rem solid #E5E5E5;
    background-color: #FFFFFF;
  }
  
  .oper-btn-default {
    float: right;
    margin-right: .15rem;
    width: .8rem;
    height: .3rem;
    line-height: .3rem;
    text-align: center;
    border-radius: .3rem;
    border: .01rem solid #999999;
  }
  
  .oper-btn {
    color: #FFFFFF;
    border: none;
    background: -webkit-linear-gradient(left, rgb(255, 161, 38), rgb(255, 139, 32));
    background: linear-gradient(to right, rgb(255, 161, 38), rgb(255, 139, 32));
  }
  
  .fr {
    float: right;
  }
  .refund-progress{
  color: #333;
  width: 100%;
  height: .5rem;
  padding-left: .15rem;
  padding-right: .15rem;
  line-height: .43rem;
  border-bottom: .05rem solid #F5F5F5;
}
.order-detail-view .hi-life-list-item{
  padding-left: .15rem;
}
</style>


